export default{
	name:'imglist',
	data(){
		return{
			isShowVideo:false,
			videoSrc:''
		}
	},
	props:{
		detailData:{
			type:Object,
			default(){
				return {}
			}
		}
	},
	watch:{
		detailData(newVal,oldVal){
			if(newVal.videoSrc != ''){
				this.videoSrc = newVal.videoSrc
				this.isShowVideo = true
				console.log('有视频')
			}else{
				this.isShowVideo = false
				console.log('没有视频')
			}
		}
	}
}
